
import React from 'react';


function MobileNav () {
    
  function menuCloseBtnHelper(){
    console.log("clicked to close")
    document.getElementById('mobile').style.display = "none";
       
  }

    return (
        <nav id="mobile">
        <div id="closeDiv" onClick={menuCloseBtnHelper}><button id="closeBtn">X</button></div>
        <ul>
            <li><a href="#projects" id="m1" onClick={menuCloseBtnHelper}>work</a></li>
            <li><a href="#about" id="m2" onClick={menuCloseBtnHelper}>about</a></li>
            <li><a href="#contact" id="m3" onClick={menuCloseBtnHelper}>contact me</a></li>
        </ul>
        
        
        </nav>
    );
}

export default MobileNav;






